import React, {useState} from 'react'
import Button from '@material-ui/core/Button';
import { TextField, Select, MenuItem, FormControl, InputLabel, FormLabel  } from '@material-ui/core';
import './minors-form.css'
const MinorsForm = ({addMinor}) => {
    const [name, setName] = useState('');
    const [surname, setSurname] = useState('');
    const [paternity, setPaternity] = useState('');
    const [birthDate, setBirthDate] = useState('2017-04-01');
    const [schoolClass, setSchoolClass] = useState('');
    const [gender, setGender] = useState('');
    const [registration, setRegistration] = useState('');
    const [motherName, setMotherName] = useState('');
    const [motherJob, setMotherJob] = useState('');
    const [motherPhone, setMotherPhone] = useState('');
    const [fatherName, setFatherName] = useState('');
    const [fatherJob, setFatherJob] = useState('');
    const [fatherPhone, setFatherPhone] = useState('');
    const [needyStatus, setNeedyStatus] = useState('');
    const [largeStatus, setLargeStatus] = useState('');
    const [dysStatus, setDysStatus] = useState('');
    const [custody, setCustody] = useState('');
    const [guardian, setGuardian] = useState('');
    const [orphanage, setOrphanage] = useState('');
    const [inschool, setInschool] = useState('');
    const [law, setLaw] = useState('');
    const [note, setNote] = useState('');
    const [language, setLanguage] = useState('');
    const [achievements, setAchievements] = useState('');
    const [assistance, setAssistance] = useState('');
    const [disability, setDisability] = useState('');
    const [homeSchooling, setHomeSchooling] = useState('');
    const [medCard, setMedCard] = useState('');
    const [photo, setPhoto] = useState(null);
    const [nameError, setNameError] = useState(false)
    const [phoneError, setPhoneError] = useState(false)
    
    const minor = {
        Surname: surname,
        Name: name,
        First_name: paternity,
        Paul: gender,
        Data_of_birth: birthDate,
        Class_school: schoolClass,
        Registration: registration,
        FCS_mother: motherName,
        Mother_workplace: motherJob,
        Phone_number_mom: motherPhone,
        FCS_father: fatherName,
        Father__workplace: fatherJob,
        Phone_number_dad: fatherPhone,
        Needy_family: needyStatus,
        Large_family: largeStatus,
        Dysfunctional_family: dysStatus,
        Custody: custody,
        Guardian: guardian,
        Information_pupil_orphanage: orphanage,
        Information_inschool_accounting: inschool,
        Information_law_enforcement_agencies: law,
        Note: note,
        Information_about_the_language_of_instruction: language,
        Information_about_the_achievements: achievements,
        Information_about_social_assistance: assistance,
        Disability_information: disability,
        Information_about_homeschooling: homeSchooling,
        Availability_of_a_medical_card: medCard,
        foto: photo
  
    }
   
    // const readFile = (value) => {
    //       const reader = new FileReader()
    //       reader.readAsDataURL(value)
    //       reader.onload = function() {
    //         setPhoto(reader.result)
    //       };

    // }
   const SendInfo = () => {
     addMinor(minor)
   }
   
    
   
    
    
    return (
        <form className="form" noValidate autoComplete="off">
            <FormLabel className="form__label">  </FormLabel>
            <TextField  required margin="normal"  label="" value={surname} onChange={ (e) => setSurname(e.target.value)}/>
            <TextField required error={nameError} margin="normal"  label="" value={name} onChange={ (e) => setName(e.target.value)}/>
            <TextField  required margin="normal"  label="" value={paternity} onChange={ (e) => setPaternity(e.target.value)}/>
            <FormControl required margin="normal">
              <InputLabel id="demo-simple-select-label"></InputLabel>
              <Select  className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
              value={gender}
              onChange={ e => setGender(e.target.value)}>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select>
            </FormControl>
            
            <TextField  required margin="normal"  label=" " value={birthDate} type="date" onChange={ (e) => setBirthDate(e.target.value)}/>
            <FormControl required margin="normal">
              <InputLabel id="demo-simple-select-label"> </InputLabel>
              <Select className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
              value={schoolClass}
              onChange={ e => setSchoolClass(e.target.value)}>
                <MenuItem value="1">1</MenuItem>
                <MenuItem value="2">2</MenuItem>
                <MenuItem value="3">3</MenuItem>
                <MenuItem value="4">4</MenuItem>
                <MenuItem value="5">5</MenuItem>
                <MenuItem value="6">6</MenuItem>
                <MenuItem value="7">7</MenuItem>
                <MenuItem value="8">8</MenuItem>
                <MenuItem value="9">9</MenuItem>
                <MenuItem value="10">10</MenuItem>
                <MenuItem value="11">11</MenuItem>
              </Select>
            </FormControl>
            <TextField  required margin="normal"  label=" " value={registration} onChange={ (e) => setRegistration(e.target.value)}/>
          <div className="form__row">
            <div className="form__part">
              <FormLabel className="form__label">  </FormLabel>
              <TextField  margin="normal"  label=" " value={motherName} onChange={ (e) => setMotherName(e.target.value)}/>
              <TextField  margin="normal"  label="  " value={motherJob} onChange={ (e) => setMotherJob(e.target.value)}/>
              <TextField error={phoneError} margin="normal" className = "form__input"  label=" " type="number" helperText=" : 88005553535" value={motherPhone} onChange={ e => setMotherPhone(e.target.value)}/>
            </div>
            <div className="form__part">
              <FormLabel className="form__label">  </FormLabel>
              <TextField  margin="normal"  label=" " value={fatherName} onChange={ (e) => setFatherName(e.target.value)}/>
              <TextField  margin="normal"  label="  " value={fatherJob} onChange={ (e) => setFatherJob(e.target.value)}/>
              <TextField margin="normal" className = "form__input"  label=" " type="number" helperText=" : 88005553535" value={fatherPhone} onChange={ e => setFatherPhone(e.target.value)}/>
            </div>
          </div>
          <div className="form__row">
          <div className="form__part form--shrink">
          <FormLabel className="form__label"> </FormLabel>
          <FormControl margin="normal" >
            <InputLabel id="demo-simple-select-label"> </InputLabel>
            <Select  className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
            value={needyStatus}
            onChange={ e => setNeedyStatus(e.target.value)}>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
          <FormControl margin="normal" >
            <InputLabel id="demo-simple-select-label"> </InputLabel>
            <Select  className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
            value={largeStatus}
            onChange={ e => setLargeStatus(e.target.value)}>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
          <FormControl margin="normal" >
            <InputLabel id="demo-simple-select-label"> </InputLabel>
            <Select  className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
            value={dysStatus}
            onChange={ e => setDysStatus(e.target.value)}>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
          </div>
          <div className="form__part">
          <FormLabel className="form__label">  </FormLabel>
          <TextField  margin="normal"  label="  " value={custody} onChange={ (e) => setCustody(e.target.value)}/>
          <TextField  margin="normal"  label="" value={guardian} onChange={ (e) => setGuardian(e.target.value)}/>
          <TextField  margin="normal"  label=",    " value={orphanage} onChange={ (e) => setOrphanage(e.target.value)}/>
          </div>
          </div>
          <FormLabel className="form__label"> </FormLabel>
          <TextField  margin="normal"  label="   " value={inschool} onChange={ (e) => setInschool(e.target.value)}/>
          <TextField  margin="normal"  label="      " value={law} onChange={ (e) => setLaw(e.target.value)}/>
          <TextField  margin="normal"  label="" value={note} onChange={ (e) => setNote(e.target.value)}/>
          <TextField  margin="normal"  label="   " value={language} onChange={ (e) => setLanguage(e.target.value)}/>
          <TextField  margin="normal"  label="  " value={achievements} onChange={ (e) => setAchievements(e.target.value)}/>
          <TextField  margin="normal"  label="   " value={assistance} onChange={ (e) => setAssistance(e.target.value)}/>
          <TextField  margin="normal"  label="  " value={disability} onChange={ (e) => setDisability(e.target.value)}/>
          <TextField  margin="normal"  label="    " value={homeSchooling} onChange={ (e) => setHomeSchooling(e.target.value)}/>
          <FormControl margin="normal">
            <InputLabel id="demo-simple-select-label">  </InputLabel>
            <Select  className = "form__select" labelId="demo-simple-select-label" id="demo-simple-select"
            value={medCard}
            onChange={ e => setMedCard(e.target.value)}>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
          {/* <input  margin="normal"  type="file"  onChange={ (e) => readFile(e.target.files[0])} accept=".png" encType="multipart/formdata"/>   */}
          <Button className = "form__button" variant="contained" color="primary" size="small" 
          onClick={() => SendInfo()}> </Button>
        </form>
    )
}

export default MinorsForm